@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-primary: #5791F8;
  --color-light-theme: #e4e9ed;
  --color-dark-theme: #191C23;
}

.app {
  width: 100vw;
  height: 100vh;
  position: relative;
  font-family: 'Fira Sans',
  sans-serif;
  color: #191C23;
  color: var(--color-dark-theme);
}



.bg-image {
  background-image: url(/covid19-tracker/static/media/background.a0521026.png);
  height: 100%;
  filter: blur(6px);
  -webkit-filter: blur(6px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  /* background-color: red; */
}


 .body {
     position: absolute;
     top: 0;
     left: 0;
     right: 0;
     bottom: 0;
     text-align: center;
 }

 .absolute-center {
     display: flex;
     justify-content: center;
     align-items: center;
 }

 .mt-24 {
    margin-top: 24px;
 }

 .btn-style-1 {
     background-color: var(--color-primary);
     border-radius: 4px;
     /* font-size: 1rem; */
 }

 .btn-lg {
     padding-left: 24px;
     /* padding-right: 24px; */
     font-size: 1rem;
 }

 .logo {
     height: 150px;
 }

 .app-title {
     font-family: 'Poiret One',
         cursive;
     font-size: 2rem;
     margin-top: 18px;
     margin-bottom: 18px;
     font-weight: bold;
     color: var(--color-primary);
     text-shadow: 1px 0 0 var(--color-dark-theme),
     -1px 0 0 var(--color-primary),
     0 2px 0 var(--color-primary),
     /* 0 -2px 0 var(--color-primary), */
     /* 1px 1px var(--color-primary), */
     -1px -1px 0 var(--color-primary),
     1px -1px 0 var(--color-primary),
     -1px 1px 0 var(--color-primary);

 }

 .version {
     font-size: 1.4rem;
     margin-top: 18px;
     font-weight: bold;
     color: var(--color-dark-theme);
     letter-spacing: 0.1rem;
     
 }
